body article blockquote {
	border-inline-start: 2px solid var(--color-gray);
	font-style: italic;
}

.hint {
	@include text-base;
	@include my-2;
	@include padding-2;
	@include radius-base;
	background-color: $gray-100;
	font-style: normal;
	border-inline-start: none;

	.icon {
		display: block;
		text-align: center;
		display: table-cell;
		vertical-align: middle;
		height: auto;
		width: auto;

		svg {
			@include px-3;
			@include text-base;
			height: 1em;
			vertical-align: middle;
		}
	}

	.text {
		vertical-align: middle;
	}

	a {
		color: $font-color;
	}
}

.hint.panel {
	@include padding-4;
}

.hint.header {
	@include font-bold;
	@include px-8;
	@include radius-small;
	margin: auto -2em auto -2em;

	@media (max-width: 750px) {
		@include px-4;
		margin: auto -1em auto -1em;
	}
}

.hint.caption {
	@include text-xs;
	@include font-semibold;
	margin: -0.5rem 0 0.5rem 0;
	text-align: center;
	letter-spacing: -.01em;
	color: var(--color-gray);
	background-color: transparent;
}

