// rounded images
img {
	@include my-4;
	@include radius-base;
	display: block;
	visibility: hidden;
	max-width: 100%;
}

// image styles
article img[alt~="smallimg"] {
	@include my-4;
	@include radius-base;
	width: 50%;
	min-width: 300px;
	padding: .5rem;
	border-radius: 5px;
}

article img[alt~="crest"] {
	margin-bottom: 1rem;
	width: 25%;
	min-width: 300px;
	padding: .5rem;
	border-radius: 5px;
}

@media (min-width: 1600px) {
	article img[alt~="img"] {
		width: 140%;
		max-width: 140%;
		margin-left: -20%;
		margin-right: -20%;
		padding: .5rem;
		background-color: var(--body-background);
		box-sizing: border-box;
	}
}

// home image
.img-background {
	position: absolute;
	height: 100vh;
	width: 100%;
	margin: auto;
	top: 0;
	z-index: -1;
}

// image fade in (fade.js)
.fadeIn {
	-webkit-animation: animat_show 0.8s;
	animation: animat_show 0.8s;
}

.fadeIn,
.show-all-images img {
	visibility: visible !important;
}

@-webkit-keyframes animat_show {
	0% {
		opacity: 0
	}

	100% {
		opacity: 1
	}
}